import {Component, OnInit} from '@angular/core';
import {ErrorService} from "../../globals/services/error.service";
import {APIService} from "../../globals/services/API.service";
import {userType} from "../regisztracio/user.type";
import {HunkRichTextComponent} from  '../../components/hunk-richtexteditor/hunkRichTexteditor.component';
import {HtmlcontentsService} from  '../../globals/services/htmlcontents.service';

@Component({
    moduleId: module.id,

    templateUrl: '../regisztracio/htmls/regisztracio.html',

    selector: 'adataim-view',

    providers: [APIService],
  directives:[HunkRichTextComponent]

})
export class AdataimViewComponent implements OnInit {
    currentUser:userType = {name: '', email: '', password: ''};
    submitButtonCaption = 'Adatok módosítása';

    constructor(
      private _ErrorService:ErrorService,
      private _APIService:APIService,
      public _HtmlcontentsService:HtmlcontentsService) {
    }

;
    onRegiser() {
        this._APIService.getResponsePUT('api/currentuser/modify/', this.currentUser).subscribe(
                data=> {
                    this._ErrorService.errorClear();
                    if (data.message)
                        this._ErrorService.setErrorMsg(data.message.errors);
                    else{

                      //  this._ErrorService.setErrorMsg('Az aódatok sikeresen módosúltak');
                    }

            },
                error=> {
                console.log(error);
            })
    }

    ngOnInit() {
        this._APIService.getResponseGET('api/currentuser/', this.currentUser).subscribe(
                data=> {
                console.log(data);
                this.currentUser = data;
            },
                error=> {
                console.log(error);
            })
    }


}
